.product{
    display:flex;
    align-items: center;
    flex-direction:column;
    justify-content: flex-end;
    margin:10px;
    padding:20px;
    max-height:400px;
    min-width:100px;
    margin:10px;
    background-color: white;
    z-index:1;
}
.product_price{
    margin-top:5px;
}

.product_rating{
    display:flex;
}
.product >img{
    width:100%;
    max-height:200px;
    object-fit: contain;
    margin-bottom:15px;
}
.product_info{
    height:100px;
    margin-bottom:15px;
}
.product >button{
    background-color: #f0c14b;
    border:1px solid;
    border-color:#a88734 #9c7e31 #846a29;
}